import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  searchValue: "",
  categoryFilters: 0,
  currentPage: 1,
  sort: {
    name: "популярности",
    sortProperty: "rating",
  },
};

const filterSlice = createSlice({
  name: "filters",
  initialState,
  reducers: {
    setCategoryFilters(state, action) {
      state.categoryFilters = action.payload;
    },
    setSearchValue(state, action) {
      state.searchValue = action.payload
    },
    setSort(state, action) {
      state.sort = action.payload;
    },
    setCurrentPage(state, action) {
      state.currentPage = action.payload;
    },
    setFilters(state, action) {
      state.sort = action.payload.sort;
      state.currentPage = Number(action.payload.currentPage);
      state.categoryFilters = Number(action.payload.categoryFilters);
    },
  },
});

export const selectFilter = (state) => state.filter
export const selectFilterSort = (state) => state.filter.sort;

export const { setCategoryFilters, setSort, setCurrentPage, setFilters, setSearchValue } =
  filterSlice.actions;

export default filterSlice.reducer;
